import { useState } from "react";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import TabContext from "@mui/lab/TabContext";
import TabList from "@mui/lab/TabList";
import TabPanel from "@mui/lab/TabPanel";
import Typography from "@mui/material/Typography";

export default function ProfileTab() {
  const [value, setValue] = useState("1");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: "100%", pt: 2, minHeight: "616px" }}>
      <TabContext value={value}>
        <TabList
          onChange={handleChange}
          aria-label="lab API tabs example"
          mb="1em"
          variant="fullWidth"
        >
          <Tab label="プロフィール" value="1" />
          <Tab label="自己紹介" value="2" />
        </TabList>
        <TabPanel value="1">
          <Box w="100%">
            <Typography variant="h5" py="24px" pb="8px">
              所属
            </Typography>
            <Typography>大同大学 情報学部 情報デザイン学科</Typography>
            <Typography>浅井研究室</Typography>
            <Typography variant="h5" py="24px" pb="8px">
              出身
            </Typography>
            <Typography>岐阜県</Typography>
            <Typography variant="h5" py="24px" pb="8px">
              誕生日
            </Typography>
            <Typography>2002年1月5日</Typography>
            <Typography variant="h5" py="24px" pb="8px">
              好きな食べ物
            </Typography>
            <Typography>オムライス</Typography>
            <Typography variant="h5" py="24px" pb="8px">
              苦手なこと
            </Typography>
            <Typography>絵を描くこと</Typography>
          </Box>
        </TabPanel>

        <TabPanel value="2">
          <Typography py="12px">
            私は、WEBデザインやプログラミングが得意で、デザインだけでなく、すべての人にとって使いやすいものを目指して作品制作をしています。
          </Typography>
          <Typography py="12px">
            一例として、WEBデザインではスマホからでもパソコンからでも見やすいようなレスポンシブデザインを原則として作品制作をしています。
          </Typography>
          <Typography py="12px">
            他にも、コーディングでは関数や変数の命名規則や、関数への適切なコメントアウトなど次にコードを読む人のことを考えたコーディングも意識しています。
          </Typography>
          <Typography py="12px">
            また、チャレンジするということも忘れていません。私の知らない技術も、自ら調べ作品に落とし込みさらなるクオリティアップを日々目指しています。
          </Typography>
        </TabPanel>
      </TabContext>
    </Box>
  );
}
