import { useState } from "react";
import imgData from "./image.json";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";
import ImageListItemBar from "@mui/material/ImageListItemBar";
// import ListSubheader from "@mui/material/ListSubheader";
import IconButton from "@mui/material/IconButton";
import InfoIcon from "@mui/icons-material/Info";
import Skeleton from "@mui/material/Skeleton";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

export default function Works() {
  const [loading, setLoading] = useState(
    imgData.reduce((obj, arr) => {
      obj[arr.imgId] = true;
      return obj;
    }, {})
  );

  const [open, setOpen] = useState(
    imgData.reduce((obj, arr) => {
      obj[arr.imgId] = false;
      return obj;
    }, {})
  );

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Typography variant="h4" py="32px" pb="8px">
        作品
      </Typography>
      <ImageList cols={3}>
        {imgData.map((item) => (
          <ImageListItem
            key={item.imgId}
            cols={item.cols || 1}
            rows={item.rows || 1}
            sx={{
              aspectRatio: `${item.cols || 1}/${item.rows || 1}`
            }}
          >
            <img
              src={`https://drive.google.com/uc?export=view&id=${item.imgId}`}
              alt={item.title}
              onLoad={() => {
                setLoading({ ...loading, [item.imgId]: false });
              }}
              style={{
                aspectRatio: `${item.cols || 1}/${item.rows || 1}`,
                display: loading[item.imgId] ? "none" : "block"
              }}
            />
            <ImageListItemBar
              title={item.title}
              subtitle={item.sub}
              actionIcon={
                <IconButton
                  sx={{ color: "rgba(255, 255, 255, 0.54)" }}
                  aria-label={`info about ${item.title}`}
                  onClick={() => setOpen({ ...open, [item.imgId]: true })}
                >
                  <InfoIcon />
                </IconButton>
              }
              sx={{ display: loading[item.imgId] ? "none" : "flex" }}
            />
            <Skeleton
              variant="rectangular"
              height="100%"
              width="100%"
              sx={{ display: loading[item.imgId] ? "block" : "none" }}
            />
          </ImageListItem>
        ))}
      </ImageList>
      {imgData.map((item) => {
        return (
          <Dialog
            open={open[item.imgId]}
            onClose={() => setOpen({ ...open, [item.imgId]: false })}
            key={`${item.imgId}_dialog`}
          >
            <DialogTitle id="alert-dialog-title">{item.title}</DialogTitle>
            <DialogContent>
              <img
                src={`https://drive.google.com/uc?export=view&id=${item.imgId}`}
                alt={item.title}
                style={{ width: "100%" }}
              />
              <DialogContentText id="alert-dialog-description">
                Let Google help apps determine location. This means sending
                anonymous location data to Google, even when no apps are
                running.
              </DialogContentText>
            </DialogContent>
          </Dialog>
        );
      })}
    </Box>
  );
}
